
import 'element-ui/lib/theme-chalk/index.css'; // 
import './assets/font/iconfont.css'//
import './assets/font/iconfont'//
import ElementUI from 'element-ui';
// 
import HistoryList from "./history-list";
import JsonMenu from './json-menu';
import SpaceSelect from './space-select'
import DataInput from './data-input'
import RelationInput from './relation-input'
import RelationInputGroup from './relation-input-group';
 
// 
const components = [HistoryList, JsonMenu, SpaceSelect, DataInput, RelationInput, RelationInputGroup];
//  install  Vue  use 
const install = function(Vue) {
    Vue.use(ElementUI);
  // 
  if (install.installed) return;
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  });
};
// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  ...components,
}