<template>
<dir>
    <p>
        <span></span>
        <sd-history-list ref="his1" @load-history="fn"></sd-history-list>

    </p>
    <p>
        <span></span>
        <sd-history-list ref="his2" :showType="2" @load-history="fn"></sd-history-list>

    </p>
    <el-button type="default" size="small" @click="addHis"></el-button>
    <el-button type="default" size="small" @click="clearHis"></el-button>
</dir>
</template>

<script>
export default {
    name:'DemoHistoryList',
    methods: {
        addHis() {
            const code = Date.now();
            this.$refs.his1.setHistory(code, {
                data: code
            },'el-icon-star-off');
            this.$refs.his2.setHistory(code, {
                data: code
            });
        },
        clearHis(){
            this.$refs.his1.clearAll();
            this.$refs.his2.clearAll();
        },
        fn(obj) {
            this.$notify({
                title: '',
                message: '' + JSON.stringify(obj),
                type: 'success'
            });
        },
    },
};
</script>

